  getDataTask() {
    console.log(this.from, this.to)
    this.loading.createLoading('Cargando Tareas...')
   return new Promise((resolve, reject) => {
    this.api.getActivities2('4A21BA9F17A8EB2C582004A213127A88', this.from, this.to, '2004307A-B1CF-4109-97D1-8603A63BA6E9').subscribe(async (data: any) => {
   
      const array = [];


      for (const item of data) {
        const dat: any = await this.getActivityInfo(item.GUID)

        if (dat) {
        
          const tarea = dat.Values.filter((it) => it.apiId == 'TAREA')
          const area = dat.Values.filter((it) => it.apiId == 'AREA')
          console.log(tarea, area)
          if (tarea.length > 0 && area.length > 0) {
            dat.tarea = tarea[0].Value;
            dat.area = area[0].Value;
            dat.end = moment(dat.DispatchDateTime).add(parseFloat(dat.DurationMins), 'minutes').format('YYYY-MM-DD HH:mm')
            const time = moment(dat.DispatchDateTime).format('YYYY-MM-DD HH:mm')
            const from = moment(this.from).format('YYYY-MM-DD HH:mm')
            const to = moment(this.to).format('YYYY-MM-DD HH:mm')
            console.log(time, from, to)
            if ( time >= from && time <= to ) {
              console.log(this.areaSelect)
              if (dat.area == this.areaSelect) {
                  array.push(dat) 
              }
            
            }
            
          }
          
        }
        
      }

      console.log(array, 'array')
      this.loading.cancelLoading()

       resolve(array)

   

    }, () => this.loading.cancelLoading())
   })
  }

  async saveTask() {
    const idx = this.taskListProgramming.findIndex((item) => {
      return item.from == moment(this.from).format('YYYY-MM-DD') && item.to == moment(this.to).format('YYYY-MM-DD')
     })


    const modal = await this.mimodal.create({
      component: CargaTaskComponent,
      componentProps: {
         tkn: '4A21BA9F17A8EB2C582004A213127A88',
         data: {
            task: this.taskListProgramming[idx].task
        }
      }
    })

    await modal.present()

    const resp = await modal.onWillDismiss();

    if (resp.data) {
      _.each(this.taskListProgramming[idx].task, (it, i) => {
        const evento = this.calendarRef.nativeElement.getApi().getEventById(it.idx)

        
        evento.remove()

        this.taskListProgramming[idx].task = this.taskListProgramming[idx].task.filter((item) => item.idx != it.idx)

        
      })

      _.each(resp.data, (it, i) => {
        this.calendarRef.nativeElement.getApi().addEvent({
          title: it.tipo,
          start: moment(it.start).format('YYYY-MM-DD HH:mm'),
          end:  moment(it.end).format('YYYY-MM-DD HH:mm'),
          id: it.GUID,
          editable: false,
          allDay: false,
         // display: 'background',
          backgroundColor: this.getColor(it.tipo),


          
        }) 
        
      })

      
    }
  }

